import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Food = ({ item, editable, handleProtOnChange }) => {
  const [cals, setCals] = useState();
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
    calulateCalories();
  }, [editable]);

  const calulateCalories = () => {
    setCals(Math.round((item.carb + item.prot) * 4 + item.fat * 9));
  };

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  const _handleProtOnChange = value => {
    setProt(Number(value));
    calulateCalories();
  };

  return (
    <tr>
      <th scope="row">{item.id}</th>
      <td>{isEditable ? <Input type="text" value={item.name} /> : item.name}</td>
      <td>{isEditable ? <Input type="text" value={item.group} /> : item.group}</td>
      <td>
        {isEditable ? (
          <Input type="number" value={item.portion} />
        ) : (
          item.portion + ' ' + item.units
        )}
      </td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={item.prot}
            onChange={event => handleProtOnChange({ ...item, prot: Number(event.target.value) })}
          />
        ) : (
          _prot + ' g'
        )}
      </td>
      <td>{isEditable ? <Input type="number" value={item.carb} /> : item.carb + ' g'}</td>
      <td>{isEditable ? <Input type="number" value={item.fat} /> : item.fat + ' g'}</td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
